// import { component } from 'vue/types/umd'
// import { component } from 'vue/types/umd'
import ShowBlog from './components/ShowBlog.vue'
import ShowCat from './components/ShowCat.vue'
import ShowCss from './components/ShowCss.vue'
import ShowHtml from './components/ShowHtml.vue'
import ShowJs from './components/ShowJs.vue'
import ShowMe from './components/ShowMe.vue'
import ShowMusic from './components/ShowMusic.vue'
import ShowMyDetail from './components/ShowMyDetail.vue'
import ShowMusicDetail from './components/ShowMusicDetail.vue'
import ShowBlogDetail from './components/ShowBlogDetail.vue'
import ShowCssDetail from './components/ShowCssDetail.vue'
import ShowHtmlDetail from './components/ShowHtmlDetail.vue'
import ShowJsDetail from './components/ShowJsDetail.vue'

export default [
    {
        path: "/",
        components: {
            showblog: ShowBlog,
            showcat: ShowCat,
            showcss: ShowCss,
            showhtml: ShowHtml,
            showjs: ShowJs,
            showme: ShowMe,
            showmusic: ShowMusic,
            showblogdetail: ShowBlogDetail,
            showcssdetail: ShowCssDetail,
            showhtmldetail:ShowHtmlDetail,
            showjsdetail:ShowJsDetail
        }
    },
    {
        path: "/showmydetail",
        component: ShowMyDetail
    },
    {
        path: "/showmusicdetail",
        component:ShowMusicDetail
    },
    {
        path: "/showblogdetail",
        component:ShowBlogDetail
    },
    {
        path: "/showcssdetail",
        component:ShowCssDetail
    },
    {
        path: "/showhtmldetail",
        component:ShowHtmlDetail
    },
    {
        path: "/showjsdetail",
        component:ShowJsDetail
    }

]


