// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import Routes from './routers'

// import ShowBlog from './components/ShowBlog.vue'
// import ShowCat from './components/ShowCat.vue'
// import ShowCss from './components/ShowCss.vue'
// import ShowHtml from './components/ShowHtml.vue'
// import ShowJs from './components/ShowJs.vue'
// import ShowMe from './components/ShowMe.vue'
// import ShowMusic from './components/ShowMusic.vue'

Vue.config.productionTip = false

Vue.use(VueRouter)
Vue.use(VueResource)

// 创建路由
const router = new VueRouter({
  routes: Routes,
  mode:"history"
  // routes: [
  //   {
  //     path: '/',
  //     components: {
  //       showblog: ShowBlog,
  //       showcat: ShowCat,
  //       showcss: ShowCss,
  //       showhtml: ShowHtml,
  //       showjs: ShowJs,
  //       showme: ShowMe,
  //       showmusic:ShowMusic
  //     }
  //   }
  // ]
  // mode:"history"
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router:router,
  components: { App },
  template: '<App/>'
})



