<template>
    <div id="show-music">
        <!-- <h1>测试</h1> -->
        <div id="music" class="music-layout router-view" style="min-height: 857px;">
            <aside class="music-sidebar" id="music-siderbar">
            </aside>
            <div class="pull-load music-view state-reset window-pull-load" id="music-container">
                <div class="pull-load-body" style="transform: translate3d(0px 0px 0px);">
                    <div class="pull-load-head">
                        <div class="pull-load-head-default">
                            <i></i>
                        </div>
                        <div class="dot-emoji" style="display: none;">
                        </div>
                    </div>
                    <main class="music-main cf" id="music-main">
                        <section class="song animated fadeInUp right">
                            <div class="intro">
                                <div class="cover bgcover" style="background-image: url(../../static/images/01.png);">
                                    <div class="intro-b">
                                        <h2 class="name">fade</h2>
                                        <time class="time">2001-07-20</time>
                                    </div>
                                </div>
                            </div>
                            <div class="record df-c">
                                <div class="cd bg-cover">
                                    <div class="bg-cover-in"></div>
                                </div>
                            </div>
                        </section>
                        <section class="song animated fadeInUp left">
                            <div class="intro">
                                <div class="cover bgcover" style="background-image: url(../../static/images/02.png);">
                                    <div class="intro-b">
                                        <h2 class="name">My Soul</h2>
                                        <time class="time">2007-04-25</time>
                                    </div>
                                </div>
                            </div>
                            <div class="record df-c">
                                <div class="cd bg-cover">
                                    <div class="bg-cover-in"></div>
                                </div>
                            </div>
                        </section>
                    </main>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "showmusicdetail"
    }
</script>

<style scoped>
    .music-layout {
        width: 800px;
        margin: 0 auto;
        padding-left: 200px;
    }

    .music-layout .music-view {
        padding-top: 80px;
    }

    .pull-load.window-pull-load {
        height: auto;
        overflow-y: initial;
        overflow-x: initial;
    }

    .pull-load {
        width: 100%;
        position: relative;
    }

    .state-reset .pull-load-body {
        transition: transform .2s, -webkit-transform .2s;
    }

    .pull-load-body {
        position: relative;
    }

    .pull-load-head {
        position: absolute;
        transform: translate3d(0, -100%, 0);
        width: 100%;
    }

    .pull-load-head-default {
        text-align: center;
        font-size: 12px;
        line-height: 48px;
        color: #7676a1;
    }

    .dot-emoji {
        width: 50px;
        margin: 0 auto;
        margin-bottom: 20px;
        position: relative;
        height: 10px;
        font-size: 10px;
    }

    .music-main {
        width: 600px;
        padding: 0 20px;
    }

    .song {
        margin-bottom: 80px;
        width: 300px;
        height: 300px;
        max-width: 320px;
        max-height: 320px;
        position: relative;
    }

    .song.right {
        float: left;
    }

    .music-layout .song {
        margin-bottom: 80px;
    }

    .song .intro,
    .song .intro .cover {
        width: 100%;
        height: 100%;
    }

    .song .intro {
        position: relative;
        z-index: 10;
    }

    .music-layout .music-main .song .cover {
        background-color: #bba477;
    }

    .song .intro .intro-b {
        width: 100%;
        position: absolute;
        bottom: 0;
        left: 0;
        background: rgba(0, 0, 0, .3);
    }

    .song .intro .name{
        margin: 10px 0 6px;
        font-size: 20px;
        color: #fff;
    }
    .music-layout .music-main .song .time{
        font-family: Georgia;
        color: #cccccc;
        font-style: italic;
    }
    .song .intro .time{
        display: inline-block;
        margin-bottom: 8px;
        font-size: 14px;
    }
    .music-layout .song.right .record{
        left: 45%;
        transition: left .5s linear;
    }
    .song .record{
        width: 90%;
        height: 90%;
        position: absolute;
        background:#1A1A1A;
        border-radius: 50%;
        z-index: 9;
        top: 15px;
        transition: transform .5s,-webkit-transform .5s;
    }
    .song .record .cd{
        width: 50%;
        height: 50%;
        position: absolute;
        top: 67.5px;
        left: 67.5px;
        background: #E6BC3C;
        border-radius: 50%;
        /* justify-items: center; */
    }
    /* .bg-cover{
        background-position: 50%;
    } */
    .song .record .cd .bg-cover-in{
        width: 50%;
        height: 50%;
        position: absolute;
        background: #fff;
        border-radius: 50%;
        top: 33.75px;
        left: 33.75px;
    }
    .music-layout .song.right .record:hover{
        left: 55%;
    }
    /* .song:hover{
        left: 55%;
    } */
    .music-layout .song.left{
        float: right;
        position: relative;
        top: 350px;
    }

    .song.left .record{
        left: -35%;
        transition: transform .5s,-webkit-transform .5s;
        transition: left .5s linear;
    }
    .music-layout .song.left .record:hover{
        left: -45%;
        
    }
</style>


