<template>
    <!-- <div class="cf"> -->
    <!-- <div class="col-left fl"> -->
    <div class="home-main-box" style="background-color: rgb(120,109,93);">
        <router-link to="/showcssdetail">
            <div class="lininde left-line"></div>
            <div class="line top-line"></div>
            <div class="top-img bg-cover" style="background-image: url(../../static/images/03.png);"></div>
            <div class="post-icon hover">
                <img src="../assets/images/css.png" alt="">
            </div>
            <div class="intro">
                <h2>Css</h2>
            </div>
        </router-link>

    </div>
    <!-- </div> -->
    <!-- <div style="clear: both;"></div> -->
    <!-- </div> -->
</template>

<script>
    export default {
        name: 'show-css'
    }
</script>

<style scoped>
    /* .col-left {
        width: 350px;
    }

    .fl {
        float: left;
    } */

    .home-main-box {
        /* float: left; */
        width: 100%;
        height: 450px;
        box-shadow: 0 8px 16px hsla(40, 33%, 60%, .99);
        position: relative;
        margin-top: 30px;
        /* margin-left: 50px; */
        transition: box-shadow 0.5s linear;
        cursor: pointer;
    }

    .home-main-box:hover {
        box-shadow: 5px 10px 15px hsla(40, 33%, 60%, 99), -5px 10px 15px hsla(40, 33%, 60%, 99);
    }

    .home-main-box .top-img {
        width: 100%;
        height: 50%;
        background-size: cover;
    }

    .bg-cover {
        background-repeat: no-repeat;
        background-position: 50%;
    }

    .home-main-box .intro {
        width: 80%;
        position: absolute;
        left: 50%;
        bottom: 13%;
        transform: translateX(-50%);
        text-align: center;
        color: #ffffff;
        font-family: lucida grande, lucida sans unicode, lucida, helvetica, Hiragino Sans GB, Microsoft YaHei, WenQuanYi Micro Hei, sans-serif;
    }

    .home-main-box .intro h2 {
        font-size: 30px;
        line-height: 60px;
        font-weight: 700;
    }

    h2 {
        display: block;
        margin-block-start: 0.83em;
        margin-block-end: 0.83em;
        margin-inline-start: 0px;
        margin-inline-end: 0px;
    }

    .home-main-box .post-icon {
        width: 140px;
        height: 140px;
        background: #ffffff;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        /* padding: 20px; */
        border-radius: 50%;
        display: flex;
        -webkit-box-align: center;
        align-items: center;
        -webkit-box-pack: center;
        justify-content: center;
    }

    .home-main-box .post-icon img {
        width: 80px;
        height: 80px;
        transition: all 0.4s linear;
        /* transform: translate(); */
        /* background: #fff; */
    }

    .home-main-box .post-icon img:hover {
        /* width: 80px;
        height: 80px; */
        /* background: #fff; */
        transform: scale(1.2);
    }
</style>


