<template>
    <div id="showblogdetail">
        <div id="blog" class="blog-layout router-view" style="min-width: 857px;">
            <aside class="blog-sidebar" id="blog-sidebar">
                <div class="tags">
                    <a href="#" class="tag">
                        <div class="tag-box" style="background-color: rgb(219, 86, 64);">
                            <i style="border-right-color: rgb(219, 86, 64);"></i>
                            JavaScript
                        </div>
                    </a>
                    <a href="#" class="tag">
                        <div class="tag-box" style="background-color: rgb(94, 123, 167);">
                            <i style="border-right-color: rgb(94, 123, 167);"></i>
                            Vue
                        </div>
                    </a>
                    <a href="#" class="tag">
                        <div class="tag-box" style="background-color: rgb(24, 144, 255);">
                            <i style="border-right-color:   rgb(24, 144, 255);"></i>
                            React
                        </div>
                    </a>

                    <a href="#" class="tag">
                        <div class="tag-box" style="background-color: rgb(101, 179, 185);">
                            <i style="border-right-color:  rgb(101, 179, 185);"></i>
                            HTML
                        </div>
                    </a>
                    <a href="#" class="tag">
                        <div class="tag-box" style="background-color: rgb(76, 174, 76);">
                            <i style="border-right-color:  rgb(76, 174, 76);"></i>
                            算法
                        </div>
                    </a>
                    <a href="#" class="tag">
                        <div class="tag-box" style="background-color: rgb(120, 109, 93);">
                            <i style="border-right-color:  rgb(120, 109, 93);"></i>
                            Node
                        </div>
                    </a>
                    <a href="#" class="tag">
                        <div class="tag-box" style="background-color: rgb(230, 188, 60);">
                            <i style="border-right-color:  rgb(230, 188, 60);"></i>
                            Mongodb
                        </div>
                    </a>
                    <a href="#" class="tag">
                        <div class="tag-box" style="background-color: rgb(135, 180, 78);">
                            <i style="border-right-color: rgb(135, 180, 78);"></i>
                            CSS
                        </div>
                    </a>
                </div>
            </aside>
            <div class="pull-load blog-view state- window-pull-load" id="blog-container">
                <div class="pull-load-body" style="transform: translate3d(0px,0px,0px);">
                    <div class="pull-load-head">
                        <div class="pull-load-head-default">
                            <i></i>
                        </div>
                        <div class="dot-emoji" style="display: none;"></div>
                    </div>
                    <main class="blog-main" id="blog-main">
                        <section class="blog-article animate fadeInDown">
                            <div class="box">
                                <a href="#" class="github-corner github"
                                    style="fill: rgb(74, 183, 189);color: rgb(255, 255, 255);"></a>
                                <h2 class="hover">还没想好写点啥</h2>
                                <time>2021-03-01</time>
                                <div class="desc">
                                    感谢您的来访~
                                    <br>
                                    <span class="look-more hover">...查看更多</span>
                                </div>
                                <div class="comment hover">
                                    <!-- <div class="bubble">49</div>
                                    <div class="reply">回复</div> -->
                                </div>
                                <ul class="tags">
                                    <li></li>
                                </ul>
                            </div>

                        </section>
                    </main>
                    <div class="pull-load-footer">
                    </div>
                </div>

            </div>

        </div>
    </div>
</template>

<script>
    export default {
        name: 'showblogdetail'
    }
</script>

<style scoped>
    .blog-layout {
        width: 800px;
        margin: 0 auto;
        padding-left: 200px;
    }

    .blog-layout .blog-sidebar {
        width: 200px;
        top: 0;
        left: 162px;
    }

    .blog-sidebar {
        position: fixed;
        height: 100%;
    }

    .blog-sidebar .tags {
        margin-top: 40px;
    }

    .blog-sidebar .tag {
        display: inline-block;
        margin: 5px 10px;
    }

    .tag-box {
        display: inline-block;
        text-decoration: none;
        font-weight: 400;
        font-size: 10px;
        color: #fff;
        height: 18px;
        line-height: 18px;
        float: left;
        padding: 0 5px 0 10px;
        position: relative;
        border-radius: 0 5px 5px 0;
        margin: 5px 9px 5px 8px;
    }

    .tag-box i {
        position: absolute;
        right: 100%;
        font-size: 0;
        line-height: 0;
        border: 9px solid transparent;
    }

    .tag-box:after {
        content: " ";
        width: 4px;
        height: 4px;
        background-color: #fff;
        border-radius: 4px;
        box-shadow: 0 0 0 1px rgba(0, 0, 0, .3);
        position: absolute;
        top: 7px;
        left: 2px;
    }

    .blog-layout .blog-view {
        padding-top: 80px;
        padding-left: 100px;
    }

    .pull-load.window-pull-load {
        height: auto;
        overflow-y: initial;
        overflow-x: initial;
    }

    .pull-load {
        width: 100%;
        position: relative;
    }

    .pull-load-body {
        position: relative;
    }

    .pull-load-head {
        position: absolute;
        transform: translate3d(0, -100%, 0);
        width: 100%;
    }

    .pull-load-head-default {
        text-align: center;
        font-size: 12px;
        line-height: 48px;
        color: #7676a1;
    }

    .dot-emoji {
        width: 50px;
        margin: 0 auto;
        margin-bottom: 20px;
        position: relative;
        height: 10px;
        font-size: 10px;
    }

    .blog-layout .blog-main {
        width: 600px;
        padding: 0 20px;
        transition: margin-left .3s linear;
    }

    .blog-article:first-child {
        margin-top: 0;
    }

    .blog-article {
        width: 100%;
        margin: 40px 0;
        background-image: url(../../static/images/note-bg.jpg);
        box-shadow: 0 5px 15px 0 rgba(0, 0, 0, .08);
        padding: 10px;
        /* display: block; */
    }

    .blog-article .box {
        width: 100%;
        border: 1px dashed #c9c9c7;
        position: relative;
        /* padding: 33px 20px; */
    }

    .blog-article .github {
        position: absolute;
        right: 0;
        top: 0;
        cursor: pointer;
    }

    .github-corner {
        width: 80px;
        height: 80px;
        border: 0;
    }

    .blog-layout h2 {
        margin-top: 33px;
    }

    .blog-article h2 {
        color: #db5640;
        font-size: 18px;
        line-height: 30px;
        margin-bottom: 10px;
        text-align: center;
    }

    .blog-article time {
        display: block;
        text-align: center;
        color: #b2b2ae;
        font-size: 12px;
        margin-bottom: 22px;
        line-height: 19px;
    }

    .blog-article .desc {
        font-size: 14px;
        line-height: 30px;
        text-align: justify;
    }

    .blog-article .desc .look-more {
        color: #b2b2ae;
        font-size: 12px;
    }

    .hover {
        cursor: pointer;
    }

    .blog-article .comment {
        margin-top: 22px;
        text-align: center;
    }

    .blog-article .comment .bubble {
        width: 42px;
        height: 38px;
        display: inline-block;
        line-height: 39px;
        color: #db5640;
        font-weight: 600;
        border: 3px solid #db5640;
        border-radius: 40%;
        /* background-image: url(../../static/images/message.png); */
        /* background-color: red; */
    }

    .blog-article .comment .reply {
        color: #db5640;
        font-size: 12px;
        margin-top: 5px;
    }

    .blog-article .tags {
        /* position: absolute; */
        bottom: 5px;
        left: 200px;
    }

    .blog-article .tags li {
        list-style: none;
        /* display: inline-block; */
        /* vertical-align: middle; */
    }
</style>


